import { getComments } from '@/api/article'
export default {
    name: 'ArticleComments',
    props: {
        article: {
            type: Object,
            required: true
        }
    },
    data(){
        return {
            comments: []
        }
    },
    async mounted(){
        console.log('this.article: ', this.article)
        const { data } = await getComments(this.article.slug)
        return {
            comments: data.comments
        }
        console.log('%c⧭', 'color: #ffa640', data)
    }
}
